import React from 'react'
import { withRouter, Link } from 'react-router-dom'
import CatalogData from '../config/catalog.json'
import './Catalog.scss'
import '../config/style.css'

const Catalog = ({ match }) => {
    const tab = match.params.tab
    const catalog = CatalogData[tab]
    const catalogLabels = Reflect.ownKeys(catalog)

    return (
        <div className="catalog-container">
            <h1>{ tab }</h1>
            { catalogLabels.map(label => { // 分类标签
                const catalogList = catalog[label]
                return (
                    <div key={label}>
                        { /* 标签标题 */}
                        <h2> 
                            <span>{ label }</span>
                        </h2>
                        { /* 子目录 */}
                        <ul> 
                            { catalogList.map(catalogItem => (
                                <li key={catalogItem.id}>
                                    <Link to={`/article/${catalogItem.id}`}>
                                        <span>{ catalogItem.title }</span>
                                    </Link>
                                </li>
                            )) }
                        </ul>
                    </div>
                )
            }) }
        </div>
    )
}

export default withRouter(Catalog)