import React, { useState, useRef, useEffect } from 'react'
import { Link } from 'react-router-dom'
import './Search.scss'

function debounce (fn, delay) { // 防抖
       let timer = null
       return function (...arg) {
           const that = this 
           if (timer) {
               clearTimeout(timer) 
           }
           timer = setTimeout(function(){
               fn.apply(that, arg)
           }, delay)
       }
} 

const Search = ({ josnData }) => {
    const [ inputValue, setInputValue ] = useState('')
    const [ status, setStatus ] = useState(false)
    const [ showData, setShowData] = useState([])
    const [ allSearchData, setAllSearchData ] = useState([])
    const inputRef = useRef(null)

    const iconClick = () => { 
        const newStatus = !status
        setStatus(newStatus)
        if (inputRef && newStatus) {
            inputRef.current.focus()
        } else {
            inputRef.current.blur()
            setShowData([])
            setInputValue('')
        }
    }  
    
    const handlerSearch = (value) => { // 搜索
        if (value) {
            const newShowData = allSearchData.filter(data => data.title.includes(value))
            setShowData(newShowData)
        } else {
            setShowData([])
        }
    }

    const inputChange = (e) => { // 输入框值改变
        const value = e.target.value
        setInputValue(value)

        debounce(handlerSearch, 200)(value)
    }

    useEffect(() => { // 把数据整理成Array形式
        let arr = []
        Reflect.ownKeys(josnData).forEach(tab => {
            Reflect.ownKeys(josnData[tab]).forEach(label => {
                const result = josnData[tab][label].map(item => ({
                    id: item.id,
                    title: item.title,
                    extra: `${tab} —— ${label}`
                }))
                arr = [ ...arr, ...result ]
            })
        })
        setAllSearchData(arr)
    }, [josnData])

    return (
        <div className="search-container">
            <input 
                type="text" 
                className={`${status && 'input-show'}`}
                ref={inputRef}
                value={inputValue} 
                onChange={inputChange}
            />
            <svg onClick={iconClick} t="1607357548970" className={`icon ${status && 'icon-show'}`} viewBox="0 0 1025 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1759" width="16" height="16"><path d="M992.256 871.40352l-242.54464-206.29504c-25.06752-22.56896-51.89632-32.93184-73.54368-31.92832 57.26208-67.072 91.83232-154.07104 91.83232-249.18016 0-212.0704-171.9296-384-384-384s-384 171.9296-384 384 171.9296 384 384 384c95.08864 0 182.10816-34.59072 249.18016-91.8528-1.00352 21.66784 9.35936 48.47616 31.92832 73.54368l206.29504 242.54464c35.328 39.23968 93.02016 42.55744 128.22528 7.35232s31.88736-92.89728-7.35232-128.22528zM384 640c-141.39392 0-256-114.60608-256-256s114.60608-256 256-256 256 114.60608 256 256-114.60608 256-256 256z" p-id="1760" fill="#ffffff"></path></svg>
            { status && showData.length && 
                <ul className="show-container">
                    { showData.map(item => (
                        <li className="show-item" key={item.id}>
                            <Link to={`/article/${item.id}`} onClick={iconClick}>
                                <div className="title">{ item.title }</div>
                                <div className="extra">{ item.extra }</div>
                            </Link>
                        </li>
                    )) }
                </ul>
            }
        </div>
    )
}

export default Search