import './App.css';
import JsonData from './config/catalog.json'
import { BrowserRouter, Route } from 'react-router-dom'
import Background from './components/Background'
import Tab from './components/Tab'
import Search from './components/Search'
import Catalog from './components/Catalog'
import Article from './components/Article'

// 背景图片
const BackgroundImg = require('./config/background.jpg')

function App() {
  const tabs = Reflect.ownKeys(JsonData)

  return (
    <div className="App">
      <Background img={BackgroundImg} />
      <BrowserRouter>
        <>
          <Tab tabs={tabs} />
          <Search josnData={JsonData}/>
          <Route path="/catalog/:tab" exact component={Catalog}></Route>  
          <Route path="/article/:id" exact component={Article}></Route>  
        </>
      </BrowserRouter>
    </div>
  );
}

export default App;
